import React, { SFC, MouseEventHandler, ChangeEventHandler } from "react";
import weaccept from "../../assets/images/weaccept.png";
import fawry from "../../assets/images/fawry.png";

interface CompOwnProps {
    paymentSource: "Payment method" | "Wallet";
    paymentMethodType?: "Accept" | "Fawry";
    done: boolean;
    amount: number;
    walletBalance: number;
    fawryReferenceNumber?: string;
    changePaymentSource: ChangeEventHandler;
    changePaymentMethodType: ChangeEventHandler;
    handleSubmit: MouseEventHandler;
}

const PaymentForm: SFC<CompOwnProps> = props => {
    const {
        paymentSource,
        paymentMethodType,
        done,
        amount,
        walletBalance,
        fawryReferenceNumber,
        handleSubmit,
        changePaymentSource,
        changePaymentMethodType
    } = props;

    return (
        <main className="content">
            <div className="payment-wrapper">
                <div className="payment">
                    <h3></h3>
                    <div id="payment-form">
                        {done ? (
                            fawryReferenceNumber ? (
                                <p>Fawry reference number: {fawryReferenceNumber}</p>
                            ) : (
                                <></>
                            )
                        ) : (
                            <>
                                <div className="payment-method">
                                    <input
                                        type="radio"
                                        data-value="Wallet"
                                        onChange={changePaymentSource}
                                        checked={paymentSource === "Wallet"}
                                        className="wallet"
                                        id="wallet"
                                    />
                                    <label htmlFor="wallet"> </label>
                                    <input
                                        type="radio"
                                        data-value="Payment method"
                                        onChange={changePaymentSource}
                                        checked={paymentSource === "Payment method"}
                                        className="credit"
                                        id="credit"
                                    />
                                    <label htmlFor="credit"> </label>
                                </div>
                                {paymentSource === "Wallet" && (
                                    <div className="payment-info">
                                        <div className="item">
                                            <div className="amount">
                                                <p>{walletBalance}</p>
                                                <span>..</span>
                                            </div>
                                            <div className="name">
                                                <p> </p>
                                            </div>
                                        </div>
                                        <div className="item">
                                            <div className="amount">
                                                <p>{walletBalance - amount}</p>
                                                <span>..</span>
                                            </div>
                                            <div className="name">
                                                <p>   </p>
                                            </div>
                                        </div>
                                    </div>
                                )}
                                {paymentSource === "Payment method" && (
                                    <div className="payment-method">
                                        <input
                                            type="radio"
                                            data-value="Fawry"
                                            onChange={changePaymentMethodType}
                                            checked={paymentMethodType === "Fawry"}
                                            className="wallet"
                                            id="fawry"
                                        />
                                        <label htmlFor="fawry"></label>
                                        <input
                                            type="radio"
                                            data-value="Accept"
                                            onChange={changePaymentMethodType}
                                            checked={paymentMethodType === "Accept"}
                                            className="credit"
                                            id="accept"
                                        />
                                        <label htmlFor="accept"> </label>
                                    </div>
                                )}
                            </>
                        )}
                    </div>
                </div>
                <div className="summary">
                    <h3> </h3>
                    <div className="order">
                        <div className="item">
                            <div className="amount">
                                <p>{amount}</p>
                                <span>..</span>
                            </div>
                            <div className="name">
                                <p> </p>
                            </div>
                        </div>
                    </div>
                    {paymentSource === "Payment method" ? (
                        <div className="providor">
                            <p>
                                           
                                  .
                            </p>
                            {paymentMethodType === "Accept" && (
                                <img
                                    style={{
                                        maxWidth: "150px",
                                        maxHeight: "100px",
                                        marginTop: "10px"
                                    }}
                                    src={weaccept}
                                    alt="weAccept"
                                />
                            )}
                            {paymentMethodType === "Fawry" && (
                                <img
                                    style={{
                                        maxWidth: "150px",
                                        maxHeight: "100px",
                                        marginTop: "10px"
                                    }}
                                    src={fawry}
                                    alt="fawry"
                                />
                            )}
                        </div>
                    ) : (
                        <></>
                    )}
                    <div className="pay">
                        <button className="btn" type="submit" onClick={handleSubmit}>
                             
                        </button>
                    </div>
                </div>
            </div>

            <aside></aside>
        </main>
    );
};

export default PaymentForm;
